button {
  cursor: pointer; }

a[href]:hover {
  opacity: 0.5;
  transition: all ease 0.5s; }

button:hover {
  opacity: 0.5;
  transition: all ease 0.5s; }

li {
  list-style: none; }

section {
  margin: 0; }

article {
  margin: 0; }

.visually-hidden {
  position: absolute;
